import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import {
  generateSiteMap,
  getPageList,
  getitemList,
} from "src/redux/slices/collection";
import CircularProgress from "@mui/material/CircularProgress";
import { Button, Paper, Typography, Stack } from "@mui/material";
import { useNavigate } from "react-router";
import { PATH_DASHBOARD } from "src/routes/paths";

const Collection = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [selectedCollection, setSelectedCollection] = useState([]);
  const [selectedItem, setSelectedItem] = useState([]);
  const [selectedDomain, setSelectedDomain] = useState();
  const [selectedPages, setSelectedPages] = useState([]);
  const [isLoadingPageList, setIsLoadingPageList] = useState(false);
  const domainList = useSelector(
    (state) => state?.collection?.domain?.data?.data
  );
  const pageList = useSelector(
    (state) => state?.collection?.pageList?.data?.pageNames
  );
  const collectionList = useSelector(
    (state) => state?.collection?.collectionList?.data?.data
  );
  const itemList = useSelector(
    (state) => state?.collection?.itemList?.data?.data
  );
  const isLoading = useSelector((state) => state.collection.isLoading);
  const containerWrapperStyle = {
    display: "flex",
    height: "500px",
  };

  const headingStyle = {
    position: "sticky",
    top: 0,
    backgroundColor: "#f0f0f0",
    padding: "10px",
    borderBottom: "1px solid #ccc",
  };
  const containerStyle = {
    paddingBottom: "20px",
    paddingLeft: "20px",
    paddingRight: "20px",
    overflowY: "scroll",
    backgroundColor: "#f0f0f0",
    borderRadius: "10px",
    boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
    flexShrink: 0,
    flexGrow: 1,
    margin: "0 10px",
    WebkitOverflowScrolling: "touch",
    scrollbarWidth: "thin",
    scrollbarColor: "#7a7a7a #f0f0f0",
  };
  const centeredStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    height: "100%",
  };

  const smallWidthStyle = {
    flex: "0.65",
  };

  const bigWidthStyle = {
    flex: "1.7",
  };

  const listStyle = {
    listStyleType: "none",
    padding: "0",
  };
  const contentStyle = {
    overflowY: "auto", // Enable scrolling for the content inside the container
    height: "calc(100% - 40px)",
  };

  const listItemStyle = {
    padding: "10px 0",
  };
  const handleDomainCheck = (itemId) => {
    setSelectedDomain(itemId);
    setIsLoadingPageList(true);

    dispatch(getPageList(itemId)).then(() => {
      setIsLoadingPageList(false); // Set loading to false once page list data is loaded
    });
  };

  const handleGenerateMap = () => {
    const payload = {
      siteId: localStorage.getItem("siteId"),
      domainId: selectedDomain,
      collectionIds: selectedCollection.map((collectionId) => ({
        collectionId,
        itemIds: selectedItem
          .filter((itemId) => itemId.collectionId === collectionId)
          .map((itemId) => itemId.itemId),
      })),
      pageNames: selectedPages,
    };

    dispatch(generateSiteMap(payload))
      .then(() => {
        setIsLoadingPageList(false);
        navigate(PATH_DASHBOARD.general.sitemap);
      })
      .catch((error) => {
        console.error("Error generating sitemap:", error);
      });
  };

  const handleCollectionCheck = async (itemId) => {
    if (selectedCollection.includes(itemId)) {
      setSelectedCollection(
        selectedCollection.filter((item) => item !== itemId)
      );
    } else {
      setSelectedCollection([...selectedCollection, itemId]);
    }
  };
  const handlePageCheck = async (itemId) => {
    if (selectedPages.includes(itemId)) {
      setSelectedPages(selectedPages.filter((item) => item !== itemId));
    } else {
      setSelectedPages([...selectedPages, itemId]);
    }
  };
  const handleItemCheck = async (itemId, collectionId) => {
    console.log("jahsuwvcwe", collectionId);
    const updatedSelectedItem = [...selectedItem];

    const itemIndex = updatedSelectedItem.findIndex(
      (item) => item.itemId === itemId && item.collectionId === collectionId
    );

    if (itemIndex !== -1) {
      // If the item is already in the selected items, remove it
      updatedSelectedItem.splice(itemIndex, 1);
    } else {
      // If the item is not in the selected items, add it
      updatedSelectedItem.push({ itemId, collectionId });
    }

    setSelectedItem(updatedSelectedItem);
  };

  useEffect(() => {
    dispatch(getitemList(selectedCollection));
  }, [selectedCollection]);
  useEffect(() => {
    const defaultSelectedIds = collectionList?.defaultCollections.map(
      (item) => item.wf_id
    );
    setSelectedCollection(defaultSelectedIds);
  }, [collectionList]);

  return (
    <>
      <div style={containerWrapperStyle}>
        <div style={{ ...containerStyle, ...smallWidthStyle }}>
          <div>
            <div style={headingStyle}>
              <h2>Domain List</h2>
            </div>
            <div style={contentStyle}>
              <ul style={listStyle}>
                {domainList?.length > 0 ? (
                  domainList?.map((item) => {
                    return (
                      <li key={item.id} style={listItemStyle}>
                        <input
                          type="radio"
                          checked={selectedDomain === item._id}
                          onChange={() => handleDomainCheck(item._id)}
                          style={{ marginRight: "10px", marginTop: "10px" }}
                        />
                        {item.name}
                      </li>
                    );
                  })
                ) : (
                  <p style={{ color: "red" }}>
                    The site is still not published
                  </p>
                )}
              </ul>
            </div>
          </div>
        </div>
        <div style={{ ...containerStyle, ...smallWidthStyle }}>
          <div>
            <div style={headingStyle}>
              <h2>Page List</h2>
            </div>
            {isLoadingPageList ? (
              <div style={{ ...centeredStyle }}>
                <CircularProgress
                  disabledLink
                  sx={{
                    width: 64,
                    height: 64,
                    margin: "auto",
                    display: "block",
                    marginTop: "90px",
                  }}
                />
              </div>
            ) : (
              <div style={contentStyle}>
                {selectedDomain ? (
                  <ul style={listStyle}>
                    {pageList?.map((item) => {
                      return (
                        <li key={item.id} style={listItemStyle}>
                          <input
                            type="checkbox"
                            checked={selectedPages.includes(item)}
                            onChange={() => handlePageCheck(item)}
                            style={{ marginRight: "10px", marginTop: "10px" }}
                          />
                          {item}
                        </li>
                      );
                    })}
                  </ul>
                ) : (
                  <p style={{ color: "red" }}>Please select a domain.</p>
                )}
              </div>
            )}
          </div>
        </div>
        <div style={{ ...containerStyle, ...smallWidthStyle }}>
          <div>
            <div style={headingStyle}>
              <h2>Collection List</h2>
            </div>
            <div style={contentStyle}>
              <ul style={listStyle}>
                {selectedDomain ? (
                  collectionList?.collections.map((item) => {
                    return (
                      <li key={item.id} style={listItemStyle}>
                        <input
                          type="checkbox"
                          checked={selectedCollection.includes(item._id)}
                          onChange={() => handleCollectionCheck(item._id)}
                          style={{ marginRight: "10px", marginTop: "10px" }}
                        />
                        {item.name}
                      </li>
                    );
                  })
                ) : (
                  <p style={{ color: "red" }}>Please select a domain.</p>
                )}
              </ul>
            </div>
          </div>
        </div>
        <div style={{ ...containerStyle, ...smallWidthStyle }}>
          <div>
            <div style={headingStyle}>
              <h2>Items List</h2>
            </div>

            {isLoading ? (
              <div style={{ ...centeredStyle }}>
                <CircularProgress
                  disabledLink
                  sx={{
                    width: 64,
                    height: 64,
                    margin: "auto", // Center the CircularProgress both vertically and horizontally
                    display: "block",
                    marginTop: "90px",
                  }}
                />
              </div>
            ) : (
              <ul style={listStyle}>
                {selectedDomain ? (
                  itemList?.items?.map((item) =>
                    item.map((item1) => (
                      <li key={item1.id} style={listItemStyle}>
                        <input
                          type="checkbox"
                          style={{ marginRight: "10px", marginTop: "10px" }}
                          checked={selectedItem.some(
                            (item) =>
                              item.itemId === item1._id &&
                              item.collectionId === item1._cid
                          )}
                          onChange={() =>
                            handleItemCheck(item1._id, item1._cid)
                          }
                        />
                        {item1.name}
                      </li>
                    ))
                  )
                ) : (
                  <p style={{ color: "red" }}>Please select a collection.</p>
                )}
              </ul>
            )}
          </div>
        </div>
      </div>
      <div>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginBottom: "16px",
          }}
        >
          <Button
            key="addsite"
            disabled={!selectedDomain && selectedItem?.length === 0}
            onClick={handleGenerateMap}
            color="inherit"
            variant="outlined"
            size="medium"
            sx={{
              m: 1.5,
              mt: 8,
              flexShrink: 0,
              color: "white",
              bgcolor: "blue",
              "&:hover": {
                borderColor: "lightblue",
                bgcolor: "lightblue",
              },
            }}
          >
            Generate Sitemap
          </Button>
        </div>
      </div>
    </>
  );
};

export default Collection;
