import React from "react";
import { useDispatch, useSelector } from "react-redux";
import CircularProgress from "@mui/material/CircularProgress";
import FileCopyIcon from "@mui/icons-material/FileCopy";

const Sitemap = () => {
  const siteMap = useSelector(
    (state) => state?.collection?.sitemap?.data?.sitemap
  );
  const isLoading = useSelector((state) => state.collection.isLoading);
  const containerWrapperStyle = {
    display: "flex",
    height: "600px",
  };
  const codeStyle = {
    fontFamily: "Courier New, monospace" /* Use a monospace font */,
    color: "#2e2e2e" /* Set the text color */,
    backgroundColor: "#f1f1f1" /* Set the background color */,
    padding: "15px" /* Add some padding for readability */,
    borderRadius: "5px" /* Optional: add rounded corners */,
  };
  const centeredStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    height: "100%",
  };

  const headingStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    position: "sticky",
    top: 0,
    backgroundColor: "#f0f0f0",
    padding: "10px",
    width: "100%",
    borderBottom: "1px solid #ccc",
  };

  const containerStyle = {
    paddingBottom: "20px",
    paddingLeft: "20px",
    overflowY: "scroll",
    overflowX: "scroll",
    backgroundColor: "#f0f0f0",
    borderRadius: "10px",
    boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
    flexShrink: 0,
    flexGrow: 1,
    margin: "0 10px",
    WebkitOverflowScrolling: "touch",
    scrollbarWidth: "thin",
    scrollbarColor: "#7a7a7a #f0f0f0",
  };

  const bigWidthStyle = {
    flex: "0.9",
  };
  const handleCopyToClipboard = () => {
    const el = document.createElement("textarea"); // Create a <textarea> element
    el.value = siteMap; // Set its value to the string that you want copied
    el.setAttribute("readonly", ""); // Make it read-only to be tamper-proof
    el.style.position = "absolute";
    el.style.left = "-9999px"; // Move outside the screen to make it invisible
    document.body.appendChild(el); // Append the <textarea> element to the HTML document
    const selected =
      document.getSelection().rangeCount > 0 // Check if there is any content selected previously
        ? document.getSelection().getRangeAt(0) // Store selection if found
        : false; // Mark as false to know no selection existed before
    el.select(); // Select the <textarea> content
    document.execCommand("copy"); // Copy - only works as a result of a user action (e.g. click events)
    document.body.removeChild(el); // Remove the <textarea> element
    if (selected) {
      // If a selection existed before copying
      document.getSelection().removeAllRanges(); // Unselect everything on the HTML document
      document.getSelection().addRange(selected); // Restore the original selection
    }
    alert("Sitemap code copied to clipboard!");
  };

  return (
    <div style={containerWrapperStyle}>
      <div style={{ ...containerStyle, ...bigWidthStyle }}>
        <div>
          <div style={headingStyle}>
            <h2>SiteMap</h2>
            <FileCopyIcon
              style={{ cursor: "pointer" }}
              onClick={handleCopyToClipboard}
            />
          </div>
          {isLoading ? (
            <div style={{ ...centeredStyle }}>
              <CircularProgress
                disabledLink
                sx={{
                  width: 64,
                  height: 64,
                  margin: "auto", // Center the CircularProgress both vertically and horizontally
                  display: "block",
                  marginTop: "90px",
                }}
              />
            </div>
          ) : (
            <pre style={codeStyle}>{siteMap}</pre>
          )}
        </div>
      </div>
    </div>
  );
};

export default Sitemap;
