import { createSlice } from "@reduxjs/toolkit";
// utils
import axios from "../../utils/axios";
//
import { dispatch } from "../store";

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  user: [],
  userDetails: [],
  site: [],
};

const slice = createSlice({
  name: "wfUser",
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET EVENTS
    getUser(state, action) {
      state.isLoading = false;
      state.user = action.payload;
    },
    getSite(state, action) {
      state.isLoading = false;
      state.site = action.payload;
    },
    getUserDetails(state, action) {
      state.isLoading = false;
      state.userDetails = action.payload;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const { getUser } = slice.actions;

// ----------------------------------------------------------------------

export function getwfUser(code) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get(`api/v1/wfUser/create?code=${code}`);
      dispatch(slice.actions.getUser(response));
      localStorage.setItem("token", response.data.data.user.wf_access_token);
      return response.data;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}
export function getSites(token) {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const config = {
        headers: {
          wfauthorization: token,
        },
      };
      const response = await axios.get("api/v1/site/getSites", config);
      dispatch(slice.actions.getSite(response));
      return response.data;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

export function getwfUserDetails() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get(`api/v1/wfUser/getWfDetails`);
      dispatch(slice.actions.getUserDetails(response));
      return response.data;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------
